.button {
  display: grid;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  border: 2px solid transparent;
  
  border-radius: 8px;
  
  font-weight: 700;
  font-size: 16px;
  line-height: 14px;
  color: $color-blue;

  transition-property: background-color, color, box-shadow, border-color;
  transition-duration: 0.3s;
  cursor: pointer;

  &--secondary {
    height: 56px;
    width: 148px;

    @include onMobile {
      width: 100%;
    }

    border: 2px solid $color-blue;
    background-color: $color-wite;

    &:hover {
      background-color: $color-dark-blue;
      color: $color-wite;
      box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
      border-color: transparent;
    }

    &:active {
      box-shadow: 0 8px 14px rgba(0, 83, 181, 0.48);
    }
  }

  &--primary {
    height: 56px;
    width: 100%;
    box-shadow: 0 6px 23px rgba(0, 117, 255, 0.46);
    color: $color-wite;
    background-color: $color-blue;

    @include onMobile {
      width: 100%;
    }

    &:hover {
      background: $color-dark-blue;
      box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
    }

    &:active {
      background: $color-dark-blue;
      box-shadow: 0 8px 14px rgba(0, 83, 181, 0.48);
    }
  }
}
